import React from "react";
import ReactDOM from "react-dom";
import SecondSection from "./SecondSection";
import Tracks from './Tracks';
import AboutMe from './AboutMe';
import Contact from './Contact';
import ArrowScroll from './ArrowScroll';

class MainSection extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div className="mainSection" style={this.props.bgStyle}>
            <Tracks tRef={this.props.tRef} mSDisplay={this.props.mSDisplay[0]} />
            <ArrowScroll handleScroll={this.props.handleScroll} part="aRef" text="Dalej"/>
            <AboutMe aRef={this.props.aRef} mSDisplay={this.props.mSDisplay[1]} />
            <ArrowScroll handleScroll={this.props.handleScroll} part="cRef" text="Dalej"/>
            <Contact cRef={this.props.cRef} mSDisplay={this.props.mSDisplay[2]} />
            <ArrowScroll handleScroll={this.props.handleScroll} part="tRef" text="Do początku"/>
        </div>
    }
}

export default MainSection;