import React from "react";
import logo from './justdoit.jpg';

class AboutMe extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div className="aboutMe section" ref={this.props.aRef} style={this.props.mSDisplay}>
            <p className="sectionHeader">About Me</p>
            <div className="aboutMe__logo"></div>
            <p className="aboutMe__text">Siemanko. Jestem zwykłym człowiekiem, który od zawsze posiadał w sobie ogromne zamiłowanie do muzyki. Już jako 6 letni małolat nagrywałem z kumplem i jego siostrą tzw. "kocią muzykę" - każde napieprzało bez składu i ładu - czy to w cymbałki, czy kijkiem o krzesło, czy też pluło z zapałem we flet. To wszystko było nagrywane na klasycznym magnetofonie marki Hitachi sprowadzonym prosto z Singapuru. Zamiłowanie do przygrywek pozostało ze mną. Uczęszczałem do osiedlowego klubiku żeby rozwijać umiejętności gry na keyboardzie, przez co matka zapisała mnie później do szkoły muzycznej Ta niestety wymierzyła mojej pasji potężnego kopa prosto w jajca. Po jej ukończeniu musiałem przystopować. Jednakże po jakimś czasie powróciłem do muzykowania w domowym zaciszu dzięki Fruity Loops`owi.</p>
        </div>
    }
}

export default AboutMe;