import React from "react";

class Menu extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div className="menu">
            <div className="menu__title">Menu</div>
            <div className="menu__checker" onClick={this.props.handleMenu}>
                <span className="menu__span"></span>
                <span className="menu__span"></span>
                <span className="menu__span"></span>
            </div>
            <div className="menu__overlay" >
                <ul className="menu__list">
                    <li onClick={this.props.handleScroll} part="tRef" className="menu__li">Tracks</li>
                    <li onClick={this.props.handleScroll} part="aRef" className="menu__li">About Me</li>
                    <li onClick={this.props.handleScroll} part="cRef" className="menu__li">Contact</li>
                </ul>
            </div>
        </div>
    }
}

export default Menu;