import React from "react";
import ReactDOM from "react-dom";

class SecondSection extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return <div className="secondSection" style={this.props.bgStyle}>
            <p className="secondSection__header" style={this.props.sSStyle[0]}>My Tracks</p>
            <p className="secondSection__header" style={this.props.sSStyle[1]}>About Me</p>
            <p className="secondSection__header" style={this.props.sSStyle[2]}>Contact</p>
        </div>
    }
}

export default SecondSection;